'use client'; // Ensure that this is a Client Component (only rendered in Client Side)

import { useState } from "react"
import clsx from "clsx";

interface Task {
    id: number,
    text: string,
    isCompleted: boolean
}

interface TaskProps {
    taskRecieved: Task
}

export default function Task({ taskRecieved }: TaskProps) {

    const [isCompleted, setIsCompleted] = useState<boolean>(taskRecieved?.isCompleted ?? false);
    const [task, setTask] = useState<Task | undefined>(taskRecieved);

    const taskCompleted = "bg-green-100 text-green-800 line-through"
    const taskNotCompleted = "bg-white text-gray-800"


    // Set completed / no completed
    function handleClick() {
        setIsCompleted(!isCompleted); // task local state
        setTask({ // task global state
            ...task!,
            isCompleted: !isCompleted
        });

    }

    return (
        <div
            onClick={handleClick}
            className={clsx(
                "p-3 rounded-xl cursor-pointer transition-all duration-200",
                isCompleted
                    ? taskCompleted
                    : taskNotCompleted
            )}
        >
            {taskRecieved.text}
        </div>
    )
}